"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import styles from "./ProfileHeader.module.css";

import { getOrganizationProfileApi } from "../../../../lib/api/organizer.api";
import { getUserData } from "../../../../lib/auth";

export default function ProfileHeader({ coverColor = "#7f00ff" }) {
  const router = useRouter();
  const [profile, setProfile] = useState(null);
  const [loading, setLoading] = useState(true);

  /* ================= FETCH PROFILE ================= */
  useEffect(() => {
    async function loadProfile() {
      try {
        const user = getUserData();
        if (!user?.identity) return;

        const res = await getOrganizationProfileApi(user.identity);
        if (res?.status) {
          setProfile(res.data);
        }
      } catch (err) {
        console.error("ProfileHeader error", err);
      } finally {
        setLoading(false);
      }
    }

    loadProfile();
  }, []);

  if (loading || !profile) return null;

  const firstLetter =
    profile.organizationName?.charAt(0)?.toUpperCase() || "?";

  return (
    <div className={styles.wrapper}>
      {/* COVER */}
      <div
        className={styles.cover}
        style={{ background: coverColor }}
      />

      {/* CONTENT */}
      <div className={styles.content}>
        {/* LEFT */}
        <div className={styles.left}>
          {profile.profileImage ? (
            <img
              src={profile.profileImage}
              alt="profile"
              className={styles.avatar}
            />
          ) : (
            <div className={styles.avatarFallback}>
              {firstLetter}
            </div>
          )}

          <div
            className={styles.reviews}
            onClick={() => router.push("/dashboard/profile/reviews")}
          >
            <div className={styles.stars}>⭐⭐⭐⭐⭐</div>
            <span>
              {profile.reviewsCount || 0} Reviews
            </span>
          </div>
        </div>

        {/* RIGHT */}
        <div className={styles.info}>
          <h2 className={styles.name}>
            {profile.organizationName}
            <span className={styles.role}>
              ({profile.role || "Organizer"})
            </span>
          </h2>

          <div className={styles.followInfo}>
            <span
              onClick={() =>
                router.push("/dashboard/profile/followers")
              }
            >
              {profile.followersCount || 0} Followers
            </span>

            <span
              onClick={() =>
                router.push("/dashboard/profile/following")
              }
            >
              {profile.followingCount || 0} Following
            </span>
          </div>

          <div className={styles.rank}>
            #{profile.rank || 0} Rank
          </div>
        </div>
      </div>
    </div>
  );
}
